
<template>
  <div>
    <br/>
    <label for="birth">{{msg}}</label>
    <!-- <input id="birth" type="date" v-model="birthday" name="user_date" /> -->
    <el-date-picker
        v-model="birthday"
        type ="date"
        placeholder="what's your birthday?" @change='convert'>
    </el-date-picker>
    <br/>
    <br/> 
    <h1>{{ title }}</h1>
    <p class="container">
        更多育儿交流请关注微信公众号：kittybabycare（或搜索小猫咪育儿）      
    </p>
    <img src="../assets/qrcode.jpg">
    
    <!-- <button class="btn" @click='convert'>计算</button> -->
    <div class="hint">
    本站点承诺不会记录用户任何数据</div>
</div>
</template>

<script>
import moment from "moment";
import "moment/locale/zh-cn";
moment.locale("zh-cn");

export default {
  name: "EEC",
  props: {
    msg: String
  },
  data() {
    return {
      birthday: "",
      title: "距离高考还有?天"
    };
  },
  mounted() {},
  methods: {
    convert: function() {
      // var date1=new Date('2037,6,7')
      if (this.birthday) {
        var date1 = new Date(this.birthday);
        var examDate = new Date(date1);
        if (date1.getMonth > 8) {
          // 晚一年上学
          examDate.setFullYear(date1.getFullYear() + 19);
        } else {
          examDate.setFullYear(date1.getFullYear() + 18);
        }
        // 2003年起高考为6/7
        if (date1.getTime() <= new Date("1984.8.31")) {
          examDate.setMonth(6);
        } else {
          examDate.setMonth(5);
        }
        examDate.setDate(7);
        var date2 = new Date();
        var date3 = examDate.getTime() - date2.getTime();
        var days = Math.floor(date3 / (24 * 3600 * 1000));
        if (date3 < 0) {
          this.title = `您在${-days}天前经历了高考`;
        } else {
          this.title = `距离高考还有${days}天`;
        }
      } else {
        this.title = "距离高考还有？天";
      }
      return days;
    }
  }
};
</script>

<style scoped>
h1 {
  font-size: 0.6rem;
}
label {
  font-size: 0.34rem;
}
.btn {
  background-color: #3385ff;
  color: #fff;
  /* border-radius: 20%; */
  width: 100px;
  height: 40px;
}
.hint {
  position: absolute;
  bottom: 0.5rem;
  left: 0;
  width: 100%;
  font-size: 0.28rem;
}
.container {
  font-size: 0.32rem;
}
img {
  width: 2rem;
}
h1{
  margin-bottom: 2rem;
}
</style>